






















var dateUtils = require('../../../common/util.js').dateUtils;
var limit = 10,
	page = 1;
var data = {
	/* "cmsNews.moduleId": moduleId, */
	"page": page,
	"limit": limit
}
export default {
	data() {
		return {
			listData: [],
			reload: false
		}
	},
	onLoad() {
		this.getList();
	},
	onPullDownRefresh() {
		this.reload = true;
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			data.page = page++;
			var url = "http://111.19.140.83/cmsNews/list.do";
			uni.request({
				url: url,
				data: data,
				success: (data) => {
					var rsp = data.data;
					if (rsp.code == 200) {
						let list = this.setTime(rsp.data);
						this.listData = this.reload ? list : this.listData.concat(list);
						this.reload = false;
					}
				},
				fail: (rsp, code) => {
					console.log('fail' + JSON.stringify(rsp));
				}
			})
		},
		goDetail: function(e) {
			let detail = {
				moduleName: e.moduleName,
				cover: e.cover,
				id: e.id,
				createTime: e.createTime,
				title: e.title
			}
			uni.navigateTo({
				url: "new_detail/new_detail?detail=" + encodeURIComponent(JSON.stringify(detail))
			})
		},
		setTime: function(items) {
			var newItems = [];
			items.forEach((e) => {
				newItems.push({
					moduleName: e.moduleName,
					icon:e.icon,
					id: e.id,
					createTime: dateUtils.format(e.createTime),
					title: e.title
				});
			});
			return newItems;
		}
	},
}



